import Highlighter from "react-highlight-words"
import { getExpressionIds } from "@rp/react/tanstack-api/user-data-queries"
import { useQueryContext } from "@rp/react/web/common/providers"

export const useKeywordHighlighter = (str = "") => {
  const {
    savedQuery: {
      query: { expression },
    },
  } = useQueryContext()

  const keywords: Array<string> = expression
    ? getExpressionIds(expression, { types: ["keyword"] })
    : []

  return (
    <Highlighter
      autoEscape
      highlightStyle={{ fontStyle: "italic", color: "#FDE399" }}
      highlightTag="span"
      searchWords={keywords}
      textToHighlight={str}
    />
  )
}
